/********other mixins **********/
.modal-content {
  border: none;
  max-width: 480px;
  position: absolute;
  top: 100px;
}
.modal-content .popup-box {
  padding: 10px;
  z-index: 9999;
}
.modal-content .modal-title {
  font-weight: 600;
  font-size: 0.812rem;
}
@media only screen and (min-width: 992px) {
  .modal-content .modal-title {
    font-size: 1.125rem;
  }
}
.modal-content .modal-header {
  border-bottom: none;
}
.modal-content .modal-text {
  margin-bottom: 0px;
}
.modal-content .modal-text .delivery-fee {
  padding: 8px;
  font-size: 0.812rem;
  color: #444444;
  font-weight: 600;
  margin-bottom: -7px;
  padding-left: 0px;
}
@media only screen and (min-width: 992px) {
  .modal-content .modal-text .delivery-fee {
    font-size: 0.875rem;
  }
}
.modal-content .modal-uppertext {
  font-size: 0.75rem;
}
@media only screen and (min-width: 992px) {
  .modal-content .modal-uppertext {
    font-size: 0.875rem;
  }
}

.cart-total-left img {
  height: 15px;
  margin-bottom: 2px;
}
.cart-total-left .btn {
  padding: 0px;
}

.modal-dialog {
  z-index: 1200;
  max-width: 627px;
}
.modal-dialog .modal-footer {
  border-top: none;
  padding: 5px 10px;
}
.modal-dialog .modal-footer .button-1 {
  font-size: 0.937rem;
  color: #ffffff;
  width: 30%;
  height: 30px;
  font-weight: 600;
}
@media only screen and (min-width: 992px) {
  .modal-dialog .modal-footer .button-1 {
    width: 13%;
    height: 40px;
  }
}

.modal-backdrop.fade.show {
  position: unset;
}

.modal-body {
  color: #8c8c8c;
  margin-top: -9px;
}
@media only screen and (max-width: 575px) {
  .modal-body {
    padding: 0px;
  }
}
.modal-body .vendor-wise-delivery-fee-popup {
  margin-top: -12px;
}

#vendor-wise-delivery-fee-popup-button, #vendor-wise-tip-fee-popup-button {
  box-shadow: none;
  border: none;
}